import axios from 'axios';
export default {
    name: 'scraping',
    data: function () {
        return {
            running: false,
            tips: '当前无任务',
            currentPage: 1,
            totalnum: 10,
            pagesize: 10,
            blur: '',
            sortprop: '',
            sortorder: '',
            editdialog: false,
            rowrecord: [],
            scrapingrecords: []
        };
    },
    created: function () {
        console.log('init data');
        this.refresh();
    },
    mounted: function () {
        this.timer = setInterval(this.refresh, 1500);
    },
    beforeDestroy: function () {
        clearInterval(this.timer);
    },
    methods: {
        start: function () {
            axios.post('/api/scraping')
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        stop: function () {
            axios.get('/api/stopall')
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changesort: function (sortProps) {
            this.sortprop = sortProps.prop;
            this.sortorder = sortProps.order;
        },
        refresh: function () {
            var _this = this;
            var pageparam = 'page=' + this.currentPage + '&size=' + this.pagesize;
            var sortparam = '&sortprop=' + this.sortprop + '&sortorder=' + this.sortorder;
            var blurparam = '&blur=' + this.blur;
            var geturl = '/api/scrapingrecord?' + pageparam + sortparam + blurparam;
            axios.get(geturl)
                .then(function (response) {
                _this.scrapingrecords = response.data.data;
                _this.currentPage = response.data.page;
                _this.totalnum = response.data.total;
                _this.running = response.data.running;
                if (_this.running) {
                    var tasktotal = response.data.tasktotal;
                    var taskfinished = response.data.taskfinished;
                    var percentage = taskfinished / tasktotal * 100;
                    _this.tips = "刮削进度: " + percentage.toFixed(2) + '%' + " [" + taskfinished + "/" + tasktotal + "]";
                }
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleEdit: function (index, row) {
            this.rowrecord = row;
            this.editdialog = true;
        },
        dialogexit: function () {
            this.editdialog = false;
        },
        dialogupdate: function () {
            this.editdialog = false;
            axios.put('/api/scrapingrecord', this.rowrecord)
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleDelete: function (index, row) {
            var _this = this;
            axios.delete('/api/scrapingrecord/' + row.id)
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '删除成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '删除失败:' + error,
                    type: 'error'
                });
            });
        },
        handleCurrentChange: function (num) {
            this.currentPage = num;
        }
    }
};
