import axios from 'axios';
import FileBrowserDialog from './dialogs/FileBrowserDialog.vue';
export default {
    name: 'rename',
    components: {
        FileBrowserDialog: FileBrowserDialog,
    },
    data: function () {
        return {
            isDialogVisible: false,
            prenames: "",
            fixnames: "",
            renametype: 0,
            renameconf: {
                source_folder: 'E:\\Entertaiment\\ACG\\Animes',
                reg: '[\\[第 ][0-9.svidevoa\\(\\)]*[\\]話话集 ]',
                reg2: '\\.e[0-9videvoa\\(\\)]{1,}[.]',
                prefix: 'S01E',
                ext_type: '',
                base: '',
                newfix: ''
            }
        };
    },
    methods: {
        preview: function () {
            var _this = this;
            this.fixnames = "",
                this.prenames = "",
                axios.post('/api/previewrename', this.renameconf)
                    .then(function (response) {
                    for (var i in response.data.prefix) {
                        var fix = i + "." + response.data.prefix[i];
                        _this.fixnames += fix + "\r\n";
                    }
                    for (var j in response.data.todo) {
                        var src = j + "." + response.data.todo[j];
                        _this.prenames += src + "\r\n";
                    }
                })
                    .catch(function (error) {
                    console.log(error);
                });
        },
        apply: function () {
            var _this = this;
            this.fixnames = "",
                this.prenames = "",
                axios.post('/api/renamebyreg', this.renameconf)
                    .then(function (response) {
                    for (var i in response.data.prefix) {
                        var fix = i + "." + response.data.prefix[i];
                        _this.fixnames += fix + "\r\n";
                    }
                    for (var j in response.data.todo) {
                        var src = j + "." + response.data.todo[j];
                        _this.prenames += src + "\r\n";
                    }
                })
                    .catch(function (error) {
                    console.log(error);
                });
        },
        replace: function () {
            var _this = this;
            axios.post('/api/renamebyrep', this.renameconf)
                .then(function (response) {
                console.log(response);
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '重命名成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        showDialog: function () {
            this.isDialogVisible = true;
        },
        closeDialog: function () {
            this.isDialogVisible = false;
        }
    }
};
