import axios from 'axios';
import FileBrowserDialog from './dialogs/FileBrowserDialog.vue';
export default {
    name: 'transfer',
    components: {
        FileBrowserDialog: FileBrowserDialog,
    },
    data: function () {
        return {
            running: false,
            tips: '当前无任务',
            isDialogVisible: false,
            openDialogID: 1,
            folderPath: '',
            renameflag: false,
            renameprefix: "S01E",
            currentPage: 1,
            totalnum: 10,
            pagesize: 10,
            transferdata: [],
            transconfig: {
            // source_folder: '/media/Movies',
            // linktype: 0,
            // soft_prefix: '/volume1/Media/Movies',
            // output_folder: '/media/Emby/Movies',
            // escape_folder: '',
            // mark: '',
            // id: ''
            },
            selectedoption: -1,
            options: [],
        };
    },
    created: function () {
        console.log('init data');
        this.refresh();
        this.getconfs();
    },
    mounted: function () {
        this.timer = setInterval(this.refresh, 1500);
    },
    beforeDestroy: function () {
        clearInterval(this.timer);
    },
    methods: {
        onSubmit: function () {
            this.transconfig.renameflag = this.renameflag;
            this.transconfig.renameprefix = this.renameprefix;
            axios.post('/api/transfer', this.transconfig)
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        stop: function () {
            axios.get('/api/stopall')
                .then(function (response) {
                console.log(response);
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        refresh: function () {
            var _this = this;
            var pageparam = 'page=' + this.currentPage + '&size=' + this.pagesize;
            var geturl = '/api/transrecord?' + pageparam;
            axios.get(geturl)
                .then(function (response) {
                _this.transferdata = response.data.data;
                _this.currentPage = response.data.page;
                _this.totalnum = response.data.total;
                _this.running = response.data.running;
                if (_this.running) {
                    var tasktotal = response.data.tasktotal;
                    var taskfinished = response.data.taskfinished;
                    var percentage = taskfinished / tasktotal * 100;
                    _this.tips = "转移进度: " + percentage.toFixed(2) + '%' + " [" + taskfinished + "/" + tasktotal + "]";
                }
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        delrecords: function () {
            var _this = this;
            axios.delete('/api/transrecord')
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '清理成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        handleCurrentChange: function (num) {
            this.currentPage = num;
        },
        getconfs: function () {
            var _this = this;
            var geturl = '/api/transconf/all';
            axios.get(geturl)
                .then(function (response) {
                _this.options = response.data;
                if (_this.selectedoption === -1) {
                    _this.selectedoption = _this.options[0].id;
                }
                _this.changeConf();
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        changeConf: function () {
            var _this = this;
            var config = this.options.find(function (conf) {
                return conf.id == _this.selectedoption;
            });
            if (typeof config === 'undefined') {
                this.selectedoption = this.options[0].id;
                this.transconfig = this.options[0];
                return;
            }
            this.transconfig = config;
        },
        addconf: function () {
            var _this = this;
            axios.post('/api/transconf', this.transconfig)
                .then(function (res) {
                _this.transconfig = res.data;
                _this.selectedoption = _this.transconfig.id;
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '新增成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        updateconf: function () {
            var _this = this;
            axios.put('/api/transconf', this.transconfig)
                .then(function (res) {
                _this.transconfig = res.data;
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '更新成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        deleteconf: function () {
            var _this = this;
            if (this.options.length < 2) {
                this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '仅剩一份配置无法删除',
                    type: 'success'
                });
                return;
            }
            axios.delete('/api/transconf/' + this.transconfig.id)
                .then(function () {
                _this.getconfs();
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '删除成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        showSourceDialog: function () {
            this.openDialogID = 1;
            this.isDialogVisible = true;
        },
        showOutputDialog: function () {
            this.openDialogID = 2;
            this.isDialogVisible = true;
        },
        closeDialog: function () {
            this.isDialogVisible = false;
            if (this.openDialogID === 1) {
                this.transconfig.source_folder = this.folderPath;
            }
            else if (this.openDialogID === 2) {
                this.transconfig.output_folder = this.folderPath;
            }
        }
    }
};
