import axios from 'axios';
import FileBrowserDialog from './dialogs/FileBrowserDialog.vue';
export default {
    name: 'scrapingconf',
    components: {
        FileBrowserDialog: FileBrowserDialog,
    },
    data: function () {
        return {
            isDialogVisible: false,
            openDialogID: 1,
            folderPath: '',
            mode: 1,
            settings: {}
        };
    },
    mounted: function () {
        var _this = this;
        axios.get('/api/scrapingconf')
            .then(function (response) {
            console.log(response);
            _this.settings = response.data;
            if (response.data.main_mode === 3) {
                _this.mode = 4;
            }
            else {
                if (response.data.link_type === 1) {
                    _this.mode = 1;
                }
                else if (response.data.link_type === 2) {
                    _this.mode = 2;
                }
                else {
                    _this.mode = 3;
                }
            }
        })
            .catch(function (error) {
            console.log(error);
        });
    },
    methods: {
        onSubmit: function () {
            var _this = this;
            if (this.mode === 1) {
                this.settings.link_type = 1;
                this.settings.main_mode = 1;
            }
            else if (this.mode === 2) {
                this.settings.link_type = 2;
                this.settings.main_mode = 1;
            }
            else if (this.mode === 3) {
                this.settings.link_type = 0;
                this.settings.main_mode = 1;
            }
            else if (this.mode === 4) {
                this.settings.main_mode = 3;
            }
            axios.post('/api/scrapingconf', this.settings)
                .then(function () {
                _this.$message({
                    showClose: true,
                    duration: 2000,
                    message: '更新成功',
                    type: 'success'
                });
            })
                .catch(function (error) {
                console.log(error);
            });
        },
        showSourceDialog: function () {
            this.openDialogID = 1;
            this.isDialogVisible = true;
        },
        showOutputDialog: function () {
            this.openDialogID = 2;
            this.isDialogVisible = true;
        },
        closeDialog: function () {
            this.isDialogVisible = false;
            if (this.openDialogID === 1) {
                this.settings.scraping_folder = this.folderPath;
            }
            else if (this.openDialogID === 2) {
                this.settings.success_folder = this.folderPath;
            }
        }
    }
};
