import axios from 'axios';
import VueMarkdown from 'vue-markdown';
import 'github-markdown-css';
export default {
    name: 'tutorial',
    components: {
        VueMarkdown: VueMarkdown
    },
    data: function () {
        return {
            fileContent: '',
        };
    },
    created: function () {
        this.getContent();
    },
    methods: {
        getContent: function () {
            var _this = this;
            axios.get('/api/intro')
                .then(function (response) {
                console.log(response);
                _this.fileContent = response.data;
            })
                .catch(function (error) {
                console.log(error);
            });
        }
    }
};
